import * as React from 'react'
import Spin from 'arui-feather/spin'
import './styles.sass'

class SearchResults extends React.Component {
    state = {
        images: [], 
        isValid: false
    }
    componentDidMount() {
        const { results } = this.props
        console.log(results)
        const images = []
        const promise = new Promise((resolve, reject) => {
            results.map(photo => {
                const img = new Image(150, 150)
                img.src = photo.thumbnailUrl
                img.onload = () => {
                    images.push(img)
                    if(images.length === results.length) {
                        resolve(images)
                    }
                }
                return img
            })
        })
        promise.then(res => this.setState({ isValid: true }))
    }

    handleClick = ev => {
        console.log(ev)
    }
    render() {
        const { results } = this.props
        const { isValid } = this.state
        if(isValid) {
            return (
              <ul className="gallery">
                {results.map((photo) => (
                  <li key={photo.id} className="gallery__item" onClick={this.handleClick}>
                    <img src={photo.thumbnailUrl} alt={photo.title} />
                    <span className="gallery__title">{photo.title}</span>
                  </li>))
                  }
              </ul>
            )
        }
        return <Spin size='l' visible={true} className="loader"/>
    }
    
}

export default SearchResults